* {
    margin: 0;
    --green: rgb(0,200,0);
    --red: rgb(255,0,0);
}

body {
    background: rgb(200,200,255);
}

h1 {
    font-size: 40px;
    color: white;
}

.book-search {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.query-wrapper {
    background: rgb(100,100,200);
    border: 3px solid rgb(50,50,100);
}

.title-wrapper {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-left: 30px;
    cursor: pointer;
}

.input-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

    gap: 10px;
    padding: 10px 30px;
    width: fit-content;
}

input, select {
    max-width: 200px;
    min-width: 0px;
}

.btns-wrapper {
    grid-column: 1 / span 3;
    display: flex;
    gap: 10px;
    min-width: 0px;
}

.btn {
    padding: 0 10px;
    width: fit-content;
    border: 1px solid black;
    cursor: pointer;
    background: rgb(200,200,255);
}

.entry-wrapper {
    display: flex;
    align-items: center;
}

.search-entry {
    position: relative;
    margin: 10px;
    /* padding: 0 5px; */
    border: solid 3px grey;
    border-radius: 5px;
    max-width: 900px;
    height: 200px;
    overflow: hidden;
}

.select-button {
    cursor: pointer;
    text-align: center;
}
.add {
    background: rgb(220,255,220);
}
.remove {
    background: rgb(255,220,220);
}

.entry-img {
    float: left;
    margin-right: 10px;
}

.title {
    margin: 0;
    font-size: 24px;
}

.subtitle {
    margin: 0;
    font-size: 18px;
}

.toggle-expand {
    margin: auto;
    inset: auto 0 0 0;
    width: 100%;

    text-align: center;
    background: rgba(100,100,200, .9);
    color: white;
    cursor: pointer;
}

.page-track {
    display: flex;
    gap: 5px;
    margin: 5px 10px;
}

.track-btn {
    cursor: pointer;
}

.current-page {
    font-weight: bold;
}

.faded {
    color: grey;
    cursor: auto;
}

.recommendations.faded {
    color:black;
    opacity: .5;
}